.header-wrapper{
    height: 100%;
    position: fixed;
}

.header-wrapper-remove{

}

.Home-content-name {
    margin-left: auto;
    margin-right: auto;
    transition: 0.2s;
    height: 100px;
    width: 100vw;
    background-color: black;
}

.Home-content-name-smaller {
    margin-left: auto;
    margin-right: auto;
    transition: 0.2s;
    height: 20px;
    background-color: blue;
}


.Home-header{
    background-size: cover;
    background-color: rgb(36, 11, 11);
    height: 80px; 
    margin-left: auto;
    margin-right: auto;
    transition: 0.2s;
}

.Home-header-remove{
    height: 0;
    transition: 0.2s;
    background-color: rgb(255, 255, 255);
}

.home-header-text{
    font-size: 80px;
    margin-left: auto;
    margin-right: auto;
    grid-area: home-header-text;
    max-width: 980px;
}

.Home-nav-buttons{
    background-image: 
        linear-gradient(
            to bottom, 
            #ffffff 0%, 
            #0f059e5e 100%
            );    
    color: rgb(22, 21, 21);
    cursor: pointer;
    height: 45px;
    width: 65px;
    margin: -30px 0 0 600px;
    border: none;
    font-style: italic;
    font-size: 25px;
    grid-area: buttons;
    padding: 0 !important;
    z-index: 0;
    position: relative; 
}

.Home-nav-buttons:hover {
    color: rgba(255, 0, 0, 0.356);
}


.Home-nav-buttons span {
	align-items: center;
	background: white;
	display: inline-block;
	height: 100%;
	transition: background .5s ease;
	width: 100%;
}

.Home-nav-buttons:hover span {
	background: transparent;
} 

.Home-nav-buttons-after{
    background-image: 
        linear-gradient(
            to bottom, 
            #ffffff 0%, 
            #0f059e5e 100%
            );    
    color: rgb(22, 21, 21);
    cursor: pointer;
    height: 45px;
    width: 100px;
    margin-top: -30px;
    border: none;
    font-style: italic;
    font-size: 25px;
    padding: 0 !important;
    z-index: 0;
    position: relative; 
}

.Home-nav-buttons-after:hover {
    color: rgba(255, 0, 0, 0.356);
}


.Home-nav-buttons-after span {
	align-items: center;
	background: white;
	display: inline-block;
	height: 100%;
	transition: background .5s ease;
	width: 100%;
}

.Home-nav-buttons-after:hover span {
	background: transparent;
} 

